.pswp__shoot-time {
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: #fff;
  line-height: 30px;
}

.pswp__author {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  height: 50px;
  line-height: 30px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
}

.pswp__author__avatar {
  width: 30px;
  height: 30px;
  float: left;
}

.pswp__author__name {
  margin-left: 40px;
}

.pswp__ui--fit .pswp__top-bar,
.pswp__ui--fit .pswp__caption {
  background-color: rgba(0,0,0,.5);
}

.pswp__caption {
  bottom: 50px;
  min-height: 30px;
}

.pswp__caption__center {
  color: #fff;
  padding-bottom: 0;
}
.pswp__img--placeholder {
  opacity: 0 !important;
}
