
.photo-group {
  position: relative;
}

.photo-group-header {
  position: absolute;
}

.photo-group-footer {
  position: absolute;
  top: auto;
  bottom: 0;
}

.photo-item {
  position: absolute;
  overflow: hidden;
  .photo-item-inner {

  }
  .photo-item-wrapper {
    display: block;
    height: 0;
  }
  img {
    width: 100%;
  }
}





.fade-in {
  animation: fade-in .6s ease-out both;
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.photo-group-footer {
  .photo-author {
    position: absolute;
    left: $gallery-left-right-gap;
    top: 6px;
    font-size: 10px;
    line-height: 1;
    .author-name-group {
      color: #8e915c;
    }
  }
  .photo-description {
    position: absolute;
    top: 0;
    left: $gallery-left-right-gap;
    right: $gallery-left-right-gap;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      margin: 0;
      font-size: 11px;
      text-align: center;
      display: none;
      &:nth-child(1) {
        display: -webkit-box;
        overflow: hidden;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
      }
    }
  }
}
